<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process40"></a>

<p><br></p>
<p><span class="rvts9">标签篇：</span></p>
<p><img width="597" height="765" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber131.jpg"><span class="rvts18"></span>
<br><span class="rvts21">角色标签会影响角色与</span><span class="rvts20">NPC</span><span class="rvts21">对话的一些桥段，有些甚至会影响支线任务的进展，比如血月岛只有使用</span><span class="rvts20"></span><span class="rvts21">博学者</span><span class="rvts20"></span><span class="rvts21">标签才能读懂《华丽的赞美诗》，才会引出</span><span class="rvts20"></span><span class="rvts21">血月岛的秘密</span><span class="rvts20"></span><span class="rvts21">支线进展。一些特定标签会通过玩家在游戏中的一些行为（帮助了</span><span class="rvts20">NPC</span><span class="rvts21">，杀掉了好人，放过了坏人等行为）获得。一些标签是独有的，比如说种族标签，</span><span class="rvts20">6</span><span class="rvts21">名源力者伙伴的标签。一些标签是对立的，比如</span><span class="rvts20"></span><span class="rvts21">英雄</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">恶棍</span><span class="rvts20"></span><span class="rvts21">。标签会影响角色的行为和举止，比如在</span><span class="rvts20"></span><span class="rvts21">矮人秘密</span><span class="rvts20"></span><span class="rvts21">支线，使用恶棍标签就可以释放伊泽贝尔的</span><span class="rvts20"></span><span class="rvts21">世界末日</span><span class="rvts20"></span><span class="rvts21">死亡迷雾，使阿克斯城的所有人都死掉。标签无法通过复仇女神号的魔镜进行修改，只能天生选择或后天获得。在《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中，唯一可以帮助你改标签的人在无名岛，他是个黑环头目，叫做</span><span class="rvts20"></span><span class="rvts21">瘦黄男人</span><span class="rvts20"></span><span class="rvts21">，完成他的支线，你可以让他帮你改标签，但也只能该限定的几个标签。</span>
<br><span class="rvts46"><br></span></p>
<p><span class="rvts29">标签按照种族和性别分类：</span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">标签名称</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">用途</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">人类</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">用于</span><span class="rvts20">NPC</span><span class="rvts21">对话中的人类标签，但在游戏中场景较少</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">矮人</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">用于</span><span class="rvts20">NPC</span><span class="rvts21">对话中的矮人标签，可用于浮木城矮人密集区域，</span>
<br><span class="rvts21">阿克斯矮人贵族和下水道中的矮人秘密区域</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">蜥蜴</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">用于</span><span class="rvts20">NPC</span><span class="rvts21">对话中的蜥蜴标签，用于死神海岸蜥蜴车队，</span>
<br><span class="rvts21">阿克斯议事会中的古老蜥蜴王国</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">精灵</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">用于</span><span class="rvts20">NPC</span><span class="rvts21">对话中的矮人标签，可用于欢乐堡塞碧莉的精灵营地，</span>
<br><span class="rvts21">死神海岸的精灵营地，无名岛的精灵祭坛营地。</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">不死族</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">用于</span><span class="rvts20">NPC</span><span class="rvts21">对话中的不死族标签，可用于和一些灵魂的交流，神学院，</span>
<br><span class="rvts21">石墓花园等地。</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">男性</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该标签无特殊效果</span></p>
</td>
</tr>
<tr valign="top">
<td width="111" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">女性</span></p>
</td>
<td width="841" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该标签无特殊效果</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts43"></span>
<br><span class="rvts29">标签按照特殊源力者的分类：</span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">标签名称</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">用途</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">伊凡的标签可用于很多与孤狼相关的场景对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">洛许</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">洛许的标签可用于很多和她内心恶魔有关的场景对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">猩红王子</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">猩红王子的标签可用于很多与他古老王国秘密有关的场景对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">野兽</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">野兽的标签可用于很多与他调查矮人女王有关的场景对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">塞碧莉</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">塞碧莉的标签可用于很多与她寻找前主人并摆脱束缚有关的场景对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">费恩</span></p>
</td>
<td width="808" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">费恩的标签可用于很多与他以及他神秘的起源和背景有关的场景对话</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts48"></span>
<br><span class="rvts29">标签按照精神面貌的分类：</span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts26">标签名称</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts26">用途</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">士兵</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">可用于很多在与守卫，士兵交涉的场景</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">法外狂徒</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">多用于恐吓或渲染角色的桀骜不羁</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">学者</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">能读懂很多深奥的书籍，获得更多的知识，任务提示，配方等</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">贵族</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">在与很多至高无上的富人，君主对话时，会显现出礼貌和威望的一面</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">野蛮人</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">用来抗衡一些较为蛮横，侵略口气较浓的对话</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">小丑</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">用来打破僵持的气氛，或改变话题使对方欢愉</span></p>
</td>
</tr>
<tr valign="top">
<td width="112" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">神秘</span></p>
</td>
<td width="840" valign="middle" style="border-width : 1px; border-color: #e3edf5; border-style: solid;">
<p><span class="rvts21">用来表达难以理解的事物，可迷惑对方或糊弄对方</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts48"></span>
<br><span class="rvts29">按照行为或态度的分类：</span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">标签名称</span></p>
</td>
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">用途</span></p>
</td>
</tr>
<tr valign="top">
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">动物伙伴</span></p>
</td>
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">可以和动物交谈，引出更多的剧情或线索</span></p>
</td>
</tr>
<tr valign="top">
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">英雄</span></p>
</td>
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">在帮助一些弱势群体或可怜群体后，可以获得该标签</span></p>
</td>
</tr>
<tr valign="top">
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">恶人</span></p>
</td>
<td width="18" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">当做了很多坏事，消耗了很多灵魂，怂恿他人做坏事等行为可获得该标签</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts43"></span>
<br><span class="rvts21">英雄标签多指做了许多英雄主义的事迹，它与恶人标签相对立。如果恶人事件做多的话，英雄标签会被恶人标签取代。</span>
<br><span class="rvts51">和英雄标签有关的事件：</span>
<br><span class="rvts20">1. </span><span class="rvts21">在</span><span class="rvts20"></span><span class="rvts21">风平浪静</span><span class="rvts20"></span><span class="rvts21">号快要沉船的时候，尝试拯救魔导师思万（</span><span class="rvts20">Siwan</span><span class="rvts21">）</span><span class="rvts20">;</span>
<br><span class="rvts20">2. </span><span class="rvts21">在到达欢乐堡后，在支线</span><span class="rvts20"></span><span class="rvts21">蹊跷的谋杀</span><span class="rvts20"></span><span class="rvts21">中，把米果（</span><span class="rvts20">Migo</span><span class="rvts21">）的戒指交给他的女儿亚罗（</span><span class="rvts20">Yarrow</span><span class="rvts21">）；</span>
<br><span class="rvts20">3. </span><span class="rvts21">在刚到达欢乐堡后，支线</span><span class="rvts20"></span><span class="rvts21">彻底搜查</span><span class="rvts20"></span><span class="rvts21">中，成功帮助贫民窟精灵艾罗迪（</span><span class="rvts20">Elodi</span><span class="rvts21">）解围；</span>
<br><span class="rvts20">4. </span><span class="rvts21">在欢乐堡</span><span class="rvts20"></span><span class="rvts21">被囚禁的精灵</span><span class="rvts20"></span><span class="rvts21">支线中，拒绝说出小偷的名字；</span>
<br><span class="rvts20">5. </span><span class="rvts21">在死神海岸孤狼所占据的锯木厂，成功解救被困的盲女精灵撒黑拉（</span><span class="rvts20">Saheila</span><span class="rvts21">）；</span>
<br><span class="rvts20">6. </span><span class="rvts21">在浮木城</span><span class="rvts20"></span><span class="rvts21">奇怪的货物</span><span class="rvts20"></span><span class="rvts21">支线，成功帮助亥格巴（</span><span class="rvts20">Higba</span><span class="rvts21">）逃出浮木城；</span>
<br><span class="rvts20">7. </span><span class="rvts21">帮助浮木城东门不远处绞刑场上的湿婆玛丽（</span><span class="rvts20">Mari</span><span class="rvts21">），让她逃回家中；</span>
<br><span class="rvts20">8. </span><span class="rvts21">在阿克斯的</span><span class="rvts20"></span><span class="rvts21">死刑</span><span class="rvts20"></span><span class="rvts21">支线中，说服林德</span><span class="rvts20"></span><span class="rvts21">凯姆，帮助罗斯</span><span class="rvts20"></span><span class="rvts21">科偌雷免除死刑。</span>
<br><span class="rvts20">9. </span><span class="rvts21">在黑坑洞外围</span><span class="rvts20"></span><span class="rvts21">无路可逃</span><span class="rvts20"></span><span class="rvts21">支线中，拒绝与魔导师合作放火用浓烟逼出地下室中的欧文。</span>
<br><span class="rvts52"></span>
<br><span class="rvts21">恶人标签多指做了很多恶事，它与英雄标签相对立。如果英雄事迹做多的话，恶人标签会被英雄标签所取代。</span>
<br><span class="rvts51">和恶人标签有关的事件：</span>
<br><span class="rvts20">1. </span><span class="rvts21">奴役复仇女神号；</span>
<br><span class="rvts20">2. </span><span class="rvts21">在欢乐堡</span><span class="rvts20"></span><span class="rvts21">被囚禁的精灵</span><span class="rvts20"></span><span class="rvts21">支线，向格里夫（</span><span class="rvts20">Griff</span><span class="rvts21">）揭发斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">）就是偷橘子的贼；</span>
<br><span class="rvts20">3. </span><span class="rvts21">在欢乐堡</span><span class="rvts20"></span><span class="rvts21">被囚禁的精灵</span><span class="rvts20"></span><span class="rvts21">支线，在解救阿米诺（</span><span class="rvts20">Amyro</span><span class="rvts21">）之前，杀掉撒黑拉（</span><span class="rvts20">Saheila</span><span class="rvts21">），并且告诉他；</span>
<br><span class="rvts20">4. </span><span class="rvts21">在阿玛蒂亚避难所，在与格兰缇娜（</span><span class="rvts20">Gratiana</span><span class="rvts21">）的对话中，选择打碎她的灵魂罐。详见支线</span><span class="rvts20"></span><span class="rvts21">永恒的崇拜者</span><span class="rvts20"></span><span class="rvts21">；</span>
<br><span class="rvts20">5. </span><span class="rvts21">在支线</span><span class="rvts20"></span><span class="rvts21">被净化的龙人</span><span class="rvts20"></span><span class="rvts21">中，拒绝交给蓝龙净化魔杖；</span>
<br><span class="rvts20">6. </span><span class="rvts21">在黑坑洞绞刑场，杀掉汉南格（</span><span class="rvts20">Hannag</span><span class="rvts21">）的爱徒韦典（</span><span class="rvts20">Gwydian</span><span class="rvts21">），并且在黑坑外围，杀掉他的家人；</span>
<br><span class="rvts20">7. </span><span class="rvts21">在黑坑洞外围</span><span class="rvts20"></span><span class="rvts21">无路可逃</span><span class="rvts20"></span><span class="rvts21">支线中，拒绝帮助欧文逃跑，并且杀掉欧文；</span>
<br><span class="rvts20">8. </span><span class="rvts21">在石墓花园支线</span><span class="rvts20"></span><span class="rvts21">慷慨的出价</span><span class="rvts20"></span><span class="rvts21">支线中，在获得了神秘石板后，拒绝交给瑞克（</span><span class="rvts20">Ryker</span><span class="rvts21">）并杀掉他；</span>
<br><span class="rvts20">9. </span><span class="rvts21">在</span><span class="rvts20"></span><span class="rvts21">埋葬过去</span><span class="rvts20"></span><span class="rvts21">支线中，怂恿加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）杀掉乔纳森（</span><span class="rvts20">Jonathan</span><span class="rvts21">）</span><span class="rvts20">;</span>
<br><span class="rvts20">10. </span><span class="rvts21">找到黑坑洞大门外一处看守韦典房间的矮人，选择</span><span class="rvts20"></span><span class="rvts21">伤害她</span><span class="rvts20"></span><span class="rvts21">；</span>
<br><span class="rvts20">11. </span><span class="rvts21">吸取很多的灵魂源力；</span>
<br><span class="rvts20">12. </span><span class="rvts21">在阿克斯，释放死亡迷雾。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



